import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import {
  ComposableMap,
  Geographies,
  Geography,
} from "react-simple-maps";
import wisconsinCounties from "./data/wisconsin.json";
import "./styles.css";

const WisconsinMap = () => {
  const [hoveredCounty, setHoveredCounty] = useState(null);
  const navigate = useNavigate();
  const handleCountyHover = (geo) => {
    setHoveredCounty(geo.properties.NAME);
  };

  const handleCountyLeave = () => {
    setHoveredCounty(null);
  };

  const handleGoBack = () => {
    navigate("/");
  };

  return (
    <div className="container">
      <div className="back-button" onClick={handleGoBack}>
        &lt; Go back
      </div>
      <ComposableMap
        projection="geoMercator"
        projectionConfig={{
          scale: 3200,
          center: [-89.6, 44.5],
        }}
        width={800}
        height={600}
      >
        <Geographies geography={wisconsinCounties}>
          {({ geographies }) =>
            geographies.map((geo) => {
              const countyName = geo.properties.NAME;
              const color =
                hoveredCounty === countyName ? "url(#gradientId)" : "#FFF";
              return (
                <Geography
                  key={geo.rsmKey}
                  geography={geo}
                  fill={color}
                  stroke="#000"
                  strokeWidth={0.5}
                  onMouseEnter={() => handleCountyHover(geo)}
                  onMouseLeave={handleCountyLeave}
                />
              );
            })
          }
        </Geographies>
        <defs>
          <linearGradient id="gradientId" x1="0%" y1="0%" x2="100%" y2="0%">
            <stop offset="0%" stopColor="#eef2f3" />
            <stop offset="100%" stopColor="#8e9eab" />
          </linearGradient>
        </defs>
      </ComposableMap>
      <div className="county-label" data-county={hoveredCounty || "None"}>
        {hoveredCounty || "None"}
      </div>
    </div>
  );
};

export default WisconsinMap;
